import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

// 

// tab
const list={
	listing:[]
	
}

// tab
const load = {
	loading:''
}

const navmin = {
	loading:'',
}

// tab
const nonemin = {
	nonedata:''
}

// 
const city = {
	citying:''
}

// 
const travecity = {
	traveing:''
}

//
const roturn = {
	pagesid:false
}

// 
const state = {
	list,
	load,
	navmin,
	nonemin,
	city,  //citying
	travecity,
	roturn,
}

export default new Vuex.Store({
	state,
	//
	actions:{
		listact(listact,listadata){
			console.log(listadata)
			listact.commit('listmut')
		}
	},

	// 
	mutations: {
		listmuta(state, listdata) {
			// console.log(listdata)
			state.list = {
				listing: listdata
			}
		}
	},
		
	    //
		navmuta(state, pullobj) {
			console.log(pullobj)
			state.navmin = {
				loading: pullobj.loading,
				naving: pullobj.nav,
				pageid: pullobj.pageid,
				uniload: pullobj.uniload,
				nonedata: pullobj.nonedata
			}
		},


		// 
		citymuta(state,cityion){
			console.log(cityion)
			state.city = {
				citying:cityion
			}
		},
				
		// // 
		travemuta(state,cityion){
			console.log(cityion)
			state.travecity = {
				traveing:cityion
			}
		},

		// 
		roturnmuta(state,pagesid){
			state.roturn = {
				pagesid:pagesid
			}
		}
// }
})
